import React, { Component } from "react";
import ReactDOM from "react-dom";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import {
  stationTitle1,
  stemText1,
  patient1,
  options1,
  numHitsRequired1
} from "./case1";
import {
  stationTitle2,
  stemText2,
  patient2,
  options2,
  numHitsRequired2
} from "./case2";
import {
  stationTitle3,
  stemText3,
  patient3,
  options3,
  numHitsRequired3
} from "./case3";
import {
  stationTitle4,
  stemText4,
  patient4,
  options4,
  numHitsRequired4
} from "./case4";

import "./styles.css";

function ItemList(props) {
  const items = props.items;
  const listItems = items.map(item => <li key={item.toString()}>{item}</li>);
  return <ol>{listItems}</ol>;
}

class ShowOptionList extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    if (this.props.numHits >= this.props.numHitsRequired) {
      return (
        <OptionList
          itemObject={this.props.options}
          handleClick={this.props.handleClick}
        />
      );
    } else {
      return <p>You do not have enough hits. </p>;
    }
  }
}

Array.prototype.diff = function(a) {
  return this.filter(function(i) {
    return a.indexOf(i) < 0;
  });
};

class OptionList extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  renderListItem(key, index) {
    return (
      <ListItem
        button
        key={key}
        onClick={() => this.props.handleClick(this.props.itemObject[key])}
      >
        <ListItemText
          primary={key}
          secondary={"Codeword: " + this.props.itemObject[key]}
        />
      </ListItem>
    );
  }

  render() {
    return (
      <div>
        <h2>Your options and their codewords are:</h2>
        <List>
          {Object.keys(this.props.itemObject).map(
            this.renderListItem.bind(this)
          )}
        </List>
      </div>
    );
  }
}

class MainFrame extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hits: [],
      attempts: [],
      input: "",
      invalidInput: true
    };
  }

  onChangeText(e) {
    this.setState({ input: e.target.value });
    if (e.target.value.length > 3) {
      this.setState({ invalidInput: false });
    } else {
      this.setState({ invalidInput: true });
    }
  }

  onEnterText(e) {
    e.preventDefault();
    if (this.state.input.length > 3) {
      var all_hits = this.props.patient.filter(a =>
        a.includes(this.state.input)
      );
      var current_hits = all_hits.filter(x => !this.state.hits.includes(x));
      if (current_hits.length > 0) {
        this.setState(prevState => ({
          hits: [...prevState.hits, ...current_hits],
          attempts: [...prevState.attempts, this.state.input]
        }));
      } else {
        this.setState(prevState => ({
          attempts: [...prevState.attempts, this.state.input]
        }));
      }
      this.setState({ input: "" });
    }
  }

  render() {
    return (
      <div className="main-frame">
        <page>
          <div className="stem">
            <h1>CANDIDATE INSTRUCTIONS</h1>
            <div className="contact">
              jwu202 at student.monash.edu for questions. Feedback appreciated!
            </div>
            <br />
            <br />
            <div className="details">
              <b>Station No: </b> {this.props.stationNo}
              <br />
              <b>Station Title: </b> {this.props.stationTitle} <br />
              <b>Station Time: </b> Unlimited :) <br />
            </div>
            <br />
            <p>{this.props.stemText}</p>
            <b>TASKS:</b>
            <ol>
              <li>
                Achieve {this.props.numHitsRequired} hits and gather enough
                information to come to a diagnosis!
              </li>
            </ol>
            <b>IMPORTANT NOTES:</b>
            <br />
            <ul>
              <li>
                <b>DO NOT</b> change tabs until you have completed this case -
                you will lose all your information! Only change tabs once you
                have been presented with your options and selected one - then
                it's okay.
              </li>
              <li>
                Input any HoPC questions, symptoms, risk factors, or examination
                findings you would like to know.
              </li>
              <li>
                Use <b>lowercase</b> words and where possible use the most
                succinct descriptor possible (e.g. "dyspnoea" instead of
                shortness of breath).
              </li>
              <li>
                Your inputs need to be a minimum of <b> 4 characters</b>.
              </li>
              <li>
                Inputs should be medical terminology for a <b>specific </b>
                finding, symptom or risk factor.
              </li>
              <li>
                Once you have achieved <b>{this.props.numHitsRequired} hits</b>,
                you will be presented with options, and the corresponding
                codeword. Click your option and the tab name will change to the
                codeword. Then progress to the next tab, or, if this is Station
                4, send your list of 4 codewords to the live chat!
              </li>
            </ul>
          </div>
          <div>
            <form onSubmit={this.onEnterText.bind(this)}>
              <TextField
                value={this.state.input}
                error={this.state.invalidInput}
                onChange={this.onChangeText.bind(this)}
              >
                Test{" "}
              </TextField>
              <Button
                onClick={this.onEnterText.bind(this)}
                onKeyPress={ev => {
                  if (ev.key === "Enter") {
                    this.onEnterText.bind(this);
                    ev.preventDefault();
                  }
                }}
              >
                Check
              </Button>
            </form>
          </div>
          <br />
          <div>
            {this.state.hits.length >= this.props.numHitsRequired
              ? "You have options! Scroll down below."
              : "You do not have enough hits yet."}
          </div>
          <br />

          <br />
          <hr />
          <div className="results">
            <div className="hits">
              <h2>Hits</h2>
              <ItemList items={this.state.hits}> </ItemList>
            </div>
            <div className="attempts">
              <h2>Attempts</h2>
              <ItemList items={this.state.attempts}> </ItemList>
            </div>
          </div>
          <hr />
          <div className="options>">
            <ShowOptionList
              handleClick={this.props.handleClick}
              numHitsRequired={this.props.numHitsRequired}
              options={this.props.options}
              numHits={this.state.hits.length}
            />
          </div>
        </page>
      </div>
    );
  }
}

class App extends Component {
    notify = () => toast("Scroll up and go to the next tab/case or if this is the last case, post your codewords in the chat!");
    constructor(props) {
        super(props);
    this.state = {
      done1: false,
      codeword1: "",
      done2: false,
      codeword2: "",
      done3: false,
      codeword3: "",
      done4: false,
      codeword4: ""
    };
    // TODO CHANGE THIS, THIS IS HORRIBLE PRACTICE
    this.handleClick1 = this.handleClick1.bind(this);
    this.handleClick2 = this.handleClick2.bind(this);
    this.handleClick3 = this.handleClick3.bind(this);
    this.handleClick4 = this.handleClick4.bind(this);
  }

  handleClick1(word) {
      this.setState({ done1: true, codeword1: word });
      this.notify()
  }

  handleClick2(word) {
      this.setState({ done2: true, codeword2: word });
      this.notify()
  }

  handleClick3(word) {
      this.setState({ done3: true, codeword3: word });
      this.notify()
  }

  handleClick4(word) {
      this.setState({ done4: true, codeword4: word });
      this.notify()
  }

  render() {
    return (
      <div className="App">
        <Tabs>
          <TabList>
            <Tab>
              {this.state.done1
                ? this.state.codeword1.toUpperCase()
                : stationTitle1}
            </Tab>
            <Tab>
              {this.state.done2
                ? this.state.codeword2.toUpperCase()
                : stationTitle2}
            </Tab>
            <Tab>
              {this.state.done3
                ? this.state.codeword3.toUpperCase()
                : stationTitle3}
            </Tab>
            <Tab>
              {this.state.done4
                ? this.state.codeword4.toUpperCase()
                : stationTitle4}
            </Tab>
          </TabList>

          <TabPanel>
            <MainFrame
              stationNo={1}
              stationTitle={stationTitle1}
              stemText={stemText1}
              patient={patient1}
              numHitsRequired={numHitsRequired1}
              options={options1}
              handleClick={this.handleClick1}
            />
          </TabPanel>
          <TabPanel>
            <MainFrame
              stationNo={2}
              stationTitle={stationTitle2}
              stemText={stemText2}
              patient={patient2}
              numHitsRequired={numHitsRequired2}
              options={options2}
              handleClick={this.handleClick2}
            />
          </TabPanel>
          <TabPanel>
            <MainFrame
              stationNo={3}
              stationTitle={stationTitle3}
              stemText={stemText3}
              patient={patient3}
              numHitsRequired={numHitsRequired3}
              options={options3}
              handleClick={this.handleClick3}
            />
          </TabPanel>
          <TabPanel>
            <MainFrame
              stationNo={4}
              stationTitle={stationTitle4}
              stemText={stemText4}
              patient={patient4}
              numHitsRequired={numHitsRequired4}
              options={options4}
              handleClick={this.handleClick4}
            />
          </TabPanel>
        </Tabs>
      </div>
    );
  }
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
